import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";

import { VerseTransformType } from "../types";

type Props = {
  setVerseTransformType: (t: VerseTransformType) => void;
  verseTransformType: VerseTransformType;
};

export const VerseTransformSelector = ({
  setVerseTransformType,
  verseTransformType,
}: Props): JSX.Element => {
  return (
    <ToggleButtonGroup
      name="verse-type"
      type="radio"
      value={verseTransformType}
      onChange={setVerseTransformType}
      className="w-100"
    >
      <ToggleButton
        value={VerseTransformType.Full}
        id="full-verese"
        variant="dark"
        className="col-6"
      >
        Full Verse
      </ToggleButton>
      <ToggleButton
        value={VerseTransformType.FirstLineOnly}
        id="first-line"
        variant="dark"
        className="col-6"
      >
        First Line
      </ToggleButton>
    </ToggleButtonGroup>
  );
};
