import {
  LyricsTransformType,
  LineTransformType,
  VerseTransformType,
} from "../types";
import { useState } from "react";

export const useLyricsTransformType = (): [
  LyricsTransformType,
  (t: LineTransformType) => void,
  (t: VerseTransformType) => void
] => {
  const [lyricsTransformType, setLyricsTransformType] =
    useState<LyricsTransformType>({
      lineTransformType: LineTransformType.Full,
      verseTransformType: VerseTransformType.Full,
    });

  const setLineTransformType = (lineTransformType: LineTransformType) =>
    setLyricsTransformType({ ...lyricsTransformType, lineTransformType });
  const setVerseTransformType = (verseTransformType: VerseTransformType) =>
    setLyricsTransformType({ ...lyricsTransformType, verseTransformType });

  return [lyricsTransformType, setLineTransformType, setVerseTransformType];
};
