import { useState } from "react";
import { useLocalStorage } from "../hooks/useLocalStorage";

import { Section, Song } from "../types";
import { LyricsOutput } from "../components/LyricsOutput";
import { PartAndSongSelector } from "../components/PartAndSongSelector";
import { Button, Col, Container, Row } from "react-bootstrap";
import { useLyricsTransformType } from "../hooks/useLyricsTransformType";
import { LineTransformSelector } from "../components/LineTransformSelector";
import { VerseTransformSelector } from "../components/VerseTransformSelector";

const BgmcRoute = (): JSX.Element => {
  const [song, setSong] = useState<Song | null>(null);
  const [section, setSection] = useLocalStorage<Section | null>(
    "section",
    null
  );
  const [lyricsTransformType, setLineTransformType, setVerseTransformType] =
    useLyricsTransformType();

  return (
    <>
      <title>BGMC Lyrics Learner</title>
      <Container className="bg-body-tertiary">
        <Row>
          <Col xs={12} lg={4} className="mb-2">
            <PartAndSongSelector
              section={section}
              song={song}
              setSong={setSong}
              setSection={setSection}
            />
          </Col>
          <Col xs={12} lg={4} className="mb-2">
            <LineTransformSelector
              lineTransformType={lyricsTransformType.lineTransformType}
              setLineTransformType={setLineTransformType}
            />
          </Col>
          <Col xs={12} lg={4} className="mb-2">
            <VerseTransformSelector
              verseTransformType={lyricsTransformType.verseTransformType}
              setVerseTransformType={setVerseTransformType}
            />
          </Col>
        </Row>
      </Container>
      {section && song && (
        <Container className="bar bg-body-tertiary mb-1">
          <Button
            href={song.loungeLink}
            variant="light"
            size="sm"
            target="_blank"
            className="mb-2 d-block d-lg-inline-block"
          >
            Lounge Link
          </Button>
          <LyricsOutput
            lyrics={song.lyrics[section] ?? null}
            lyricsTransformType={lyricsTransformType}
          />
        </Container>
      )}
    </>
  );
};

export default BgmcRoute;
