import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";

import { LineTransformType } from "../types";

type Props = {
  setLineTransformType: (t: LineTransformType) => void;
  lineTransformType: LineTransformType;
};

export const LineTransformSelector = ({
  setLineTransformType,
  lineTransformType,
}: Props): JSX.Element => {
  return (
    <ToggleButtonGroup
      name="line-type"
      type="radio"
      value={lineTransformType}
      onChange={setLineTransformType}
      className="w-100"
    >
      <ToggleButton
        value={LineTransformType.Full}
        id="full-line"
        variant="secondary"
        className="col-4"
      >
        Full Line
      </ToggleButton>
      <ToggleButton
        value={LineTransformType.FirstLetterOnly}
        id="first-letter"
        variant="secondary"
        className="col-4"
      >
        First Letter
      </ToggleButton>
      <ToggleButton
        value={LineTransformType.FirstWordOnly}
        id="first-word"
        variant="secondary"
        className="col-4"
      >
        First Word
      </ToggleButton>
    </ToggleButtonGroup>
  );
};
