import Dropdown from "react-bootstrap/Dropdown";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import DropdownButton from "react-bootstrap/DropdownButton";

import { Section, Song } from "../types";
import data from "../data.json";

const songs = data.sort((a: Song, b: Song) => a.title.localeCompare(b.title));

const SectionStrings = {
  Tenor_1: "Tenor 1",
  Tenor_2: "Tenor 2",
  Baritone: "Baritone",
  Bass: "Bass",
};

export const PartAndSongSelector = ({
  section,
  song,
  setSong,
  setSection,
}: {
  section: Section | null;
  song: Song | null;
  setSong: (song: Song | null) => void;
  setSection: (section: Section) => void;
}): JSX.Element => {
  return (
    <ButtonGroup className="w-100">
      <DropdownButton
        as={ButtonGroup}
        title={section ? SectionStrings[section] : "Select Section"}
        className="col-6"
      >
        <Dropdown.Item onClick={() => setSection(Section.Tenor_1)}>
          Tenor 1
        </Dropdown.Item>
        <Dropdown.Item onClick={() => setSection(Section.Tenor_2)}>
          Tenor 2
        </Dropdown.Item>
        <Dropdown.Item onClick={() => setSection(Section.Baritone)}>
          Baritone
        </Dropdown.Item>
        <Dropdown.Item onClick={() => setSection(Section.Bass)}>
          Bass
        </Dropdown.Item>
      </DropdownButton>
      <Dropdown as={ButtonGroup} className="col-6">
        <Dropdown.Toggle className="text-truncate" disabled={!section}>
          {song?.title ?? "Select Song"}
        </Dropdown.Toggle>
        <Dropdown.Menu>
          {songs.map((song: Song) => (
            <Dropdown.Item key={song.title} onClick={() => setSong(song)}>
              {song.title}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    </ButtonGroup>
  );
};
